body.active {
  --bgColor : #ffffff;
  --bgColor-expenses : #ffffff;
  --bgColor-individual-expense : #ffffff;
  --bgColor-date : #ffffff;
  --bgColor-expense-price : #1261A0;
  --bgColor-chart : #ffffff;
  --bgColor-fill : #1261A0;
  --bgColor-fill-active : #58CCED;
  --bgColor-fill-before : #58CCED;
  --bgColor-form : #ffffff;
  --bgColor-btn : #1261A0;
  --text-high : #141414ee;
  --placeholder : #333333ee;
}


* {
  box-sizing: border-box;
}

html {
  font-family: 'Mulish', sans-serif;
}

body {
  margin: 0;
  background-color: var(--bgColor);
}




.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25); */
    padding: 0.5rem;
    margin: 1rem 0;
    /* border-radius: 12px; */
    background-color: var(--bgColor-individual-expense);
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: var(--text-high);
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: var(--text-high);
    background-color: var(--bgColor-expense-price);
    /* border: 1px solid white; */
    padding: 0.5rem;
    border-radius: 7px;
    margin-right: 1rem;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    color : rgb(245, 245, 245);
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    /* border: 1px solid #ececec; */
    background-color: var(--bgColor-date);
    color: var(--text-high);
    border-radius: 7px;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
.card {
    border-radius: 7px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}


.expenses {
    padding: 1rem;
    background-color: var(--bgColor-expenses);
    margin: 2rem auto;
    width: 50rem;
    max-width: 96%;
  }

  @media(max-width:400px) {
    .expenses {
      padding: 1rem 0.1rem;
    }
  }
.expenses-filter {
  color: var(--text-high);
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }

  @media(max-width:400px) {
    .expenses-filter select {
       padding: 0.5rem 1rem;
       font-size: .5rem;
    }
  }
.expenses-list {
    list-style: none;
    padding: 0;
  }
  
  .expenses-list__fallback {
    color: var(--text-high);
    text-align: center;
  }
.chart {
    padding: 1rem;
    border-radius: 7px;
    background-color: var(--bgColor-chart);
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }

  @media(max-width:400px) {
    .chart {
      padding: 1rem .2rem;
    }
  }
.chart-bar {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .chart-bar__inner {
    height: 100%;
    width: 1rem;
    /* border: 1px solid transparent; */
    border-radius: 7px;
    background-color: var(--bgColor-fill-before);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  .chart-bar__fill {
    background-color: var(--bgColor-fill);
    width: 1rem;
    transition: all 0.3s ease-out;
  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
    margin-top: .4rem;
    color: var(--text-high);
  }
.new-expense {
    background-color: var(--bgColor-form);
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 7px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    color: var(--text-high);
  }
  
  .new-expense button {
    font: inherit;
    cursor: pointer;
    padding: .5rem 1rem;
    background-color: var(--bgColor-btn);
    color: var(--text-high);
    border-radius: 7px;
    margin-right: 1rem;
    transition: -webkit-transform .2s ease;
    transition: transform .2s ease;
    transition: transform .2s ease, -webkit-transform .2s ease;
    border: none;
    color : rgb(243, 243, 243);
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    background-color: var(--bgColor-fill-active);
    -webkit-transform : scale(1.02);
            transform : scale(1.02);
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-expense button.alternative:hover,
  .new-expense button.alternative:active {
    background-color: #ddb3f8;
  }

  @media(max-width:400px) {
    .new-expense button {
      padding: .5rem 1rem;
      margin : 1rem .2rem;
      font-size: .7rem;
    }

    .new-expense {
      padding : .5rem;
    }
  }
.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    padding-top: 2rem;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border : none;
    border-bottom: 2px solid var(--bgColor-fill);
    background-color:transparent;
    outline: none;
    width: 20rem;
    max-width: 100%;
    color: var(--text-high);
  }

  .new-expense__control input:active , .new-expense__control input:focus{
    outline: none;
    border-color: var(--bgColor-fill-active);
  }

  .new-expense__control input::-webkit-input-placeholder {
    color: var(--placeholder);
    
  }

  .new-expense__control input:-ms-input-placeholder {
    color: var(--placeholder);
    
  }

  .new-expense__control input::placeholder {
    color: var(--placeholder);
    
  }

  input[type="date"]::-webkit-calender-picker-indicator {
    -webkit-filter : brightness(50%);
            filter : brightness(50%);
  }
  
  .new-expense__actions {
    text-align: right;
    margin-bottom: 2rem;
  }

  @media(max-width:400px) {
    .new-expense__control input {
      width:15rem;
    }
  }
.container {
    display: flex;
    flex-direction: column;
    align-items:center;
    color : var(--text-high);
}

p {
    margin: 2rem 0;
}
